import "../css/normalize.css";
import "../css/iframeStyle.css";
import "../js/flexible.js";
// web icon js
document.querySelector("#icon-link").href = require("../images/favicon.ico");

// copy code js
let preButton = document.querySelectorAll("button[class='preButton']");
for (let pre = 0; pre < preButton.length; pre++) {
    preButton[pre].addEventListener("click", function () {
        const textarea = this.nextElementSibling.querySelector("code");
        navigator.clipboard.writeText(textarea.textContent);
        this.style.color = "#ffe200";
        let timer = setTimeout(() => {
            this.removeAttribute("style");
        }, 4000);
        let clearTime = setTimeout(() => {
            clearTimeout(timer);
            clearTimeout(clearTime);
        }, 8000);
    });
}

const topBox = document.getElementById("goTop");
topBox.addEventListener("click", () => {
    // 点击回到顶部
    window.scrollTo({ top: 0, behavior: "smooth" });
});
function throttle(func, wait) {
    // 节流函数
    let prev = 0;
    return function (...args) {
        const now = Date.now();
        if (now - prev > wait) {
            func(...args);
            prev = now;
        }
    };
}
function scrollCallback() {
    // 滚动回调
    const scrollTop = document.documentElement.scrollTop;
    if (scrollTop > 500) {
        topBox.style.opacity = 1;
    } else {
        topBox.style.opacity = 0;
    }
}

window.addEventListener("scroll", throttle(scrollCallback, 300));
